export default {
    name: "balance",
    onShow: function () {
        this.getSetting();
        this.getNowTime(new Date());
    },
    onReachBottom: function () {
        const self = this;
        if (self.args || self.load)
            return;
        self.load = true;
        let page = self.page + 1;
        this.$request({
            url: self.$api.balance.logs,
            data: {
                page: page,
                date: self.date,
            }
        }).then(info => {
            if (info.code === 0) {
                [self.page, self.args, self.logs] = [page, info.data.list.length === 0, self.logs.concat(info.data.list)];
            }
            self.load = false;
        });
    },
    data() {
        return {
            balance: 0,
            setting: null,
            logs: null,
            page: 1,
            load: false,
            args: false,
            showHidden: false,
            date: '',
            date_a: '',
        }
    },
    methods: {
        recharge: function () {
            uni.navigateTo({url: `/pages/balance/recharge`});
        },
        password: function () {
            uni.navigateTo({url: `/pages/balance/password`});
        },
        detail: function (row) {
            uni.navigateTo({url: `/pages/balance/detail?id=` + row.id});
        },
        getRules: function () {
            uni.navigateTo({
                url: `/pages/rules/index?url=${encodeURIComponent(this.$api.balance.index)}&keys=${JSON.stringify(['setting', 'explain'])}`,
            });
        },
        getSetting: function () {
            const self = this;
            self.$request({
                url: self.$api.balance.index,
            }).then(info => {
                if (info.code === 0) {
                    this.setting = info.data.setting;
                    this.balance = info.data.balance;
                }
            });
        },
        getLog: function () {
            const self = this;
            self.$showLoading({title: `加载中`});
            this.$request({
                url: self.$api.balance.logs,
                data: {date: self.date}
            }).then(info => {
                self.$hideLoading();
                if (info.code === 0) {
                    self.logs = info.data.list;
                }
            }).catch(() => {
                self.$hideLoading();
            })
        },

        dateLess: function () {
            let date = this.date;
            let d = new Date(date);
            d.setMonth(d.getMonth() - 1);
            this.getNowTime(d);
        },
        datePlus: function () {
            let date = this.date;
            let d = new Date(date);
            d.setMonth(d.getMonth() + 1);
            this.getNowTime(d);
        },
        dateChange: function (e) {
            let date = e.detail.value;
            let d = new Date(date);
            this.getNowTime(d);
        },

        getNowTime(date) {
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            date = [year, month].map((n) => {
                n = n.toString()
                return n[1] ? n : '0' + n
            }).join('-');
            let date_a = date.replace('-', '年') + '月';
            [this.date, this.date_a, this.page, this.args] = [date, date_a, 1, false];
            this.getLog();
        },

    }
}
